.banner_image img{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.8;
}
.banner_image{
    border-bottom: 2px solid #000;
    position: relative;
    height: fit-content;
    background: #562894;
}
.banner_content{
    position: relative;
    color: #fff;
    padding: 25px 30px;
}
.share_icon{
    background: #f5f5f5;
    // float: right;
    border-radius: 100%;
    width: 35px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    color: #562894;
    margin: auto 0 auto auto;
    transition: linear 0.5s;
}
.share_icon:hover{
    background: #562894;
    color: #fff;
}
.category_name{
    color: #f7f7f7ad;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 14px;
}
.banner_footer hr{
    border-color: #c7c7c7;
}
.banner_content h5, .banner_content h1{
    text-transform: capitalize;
}