.wrapper{
    position: relative;
}
.Summary {
    font-size: 14px;
    overflow-y: auto;
    overflow-x: hidden;
    height: 150px;
    padding: 5px 15px;
    // height: 55vh;
}
/* .main_content{
    padding: 5px 15px;
} */
.main_content h6{
    color: #562894;
}
.btn_readmore{
    position: absolute;
    cursor: pointer;
    color: #562894;
    font-weight: 500;
    bottom: 0;
    width: 95%;
    background: linear-gradient(0deg, #fff 75%, #ffffff75 25%);
    padding: 2px 0;
}
.btn_readmore:hover{
    text-decoration: none;
    color: #562894;
    font-weight: normal;
}
.related_topic_keywords span{
    border: 1px solid #562894;
    color: #562894;
    padding: 2px 8px;
    margin: 5px;
    margin-left: 0;
    border-radius: 4px;
    display: inline-block;
    transition: linear 0.5s;
}
.related_topic_keywords span:hover{
    border: 1px solid #fff;
    color: #fff;
    background: #562894;
}
.related_topic_keywords{
    margin-bottom: 1rem;
}