.tab_wrapper{
    border-top: 8px solid #f7f7f7;
}
.height_set{
    // height: calc(100%-100px);
    height: calc(100vh - 100px);
}
.article_result_header{
    background: transparent;
    font-weight: 500;
    font-size: 0.9rem;
    color: #000;
}
.media_image{
    height: 45px;
    width: 45px;
}
.articles_container{
    height: calc(100vh - 500px);
    overflow-y: scroll;
}
.card_header_heading{
    background: #fff;
}
.card_header_heading h6{
    margin-bottom: 0px;
}
.media_body_content{
    color: #848484;
}
.media_body_content p{
    font-size: 13px;
    margin-bottom: 0;
}