#root{
    height: 100vh;
    overflow: hidden;
    background: #ddd;
}
.custom-navbar{
    border-bottom: 1px solid #ccc;
    height: 60px;
    padding: 0;
}
.sidebar_menu_button{
    padding: 15px;
    border-right: 1px solid #ccc;
    margin-right: 15px;
}
.custom-navbar .navbar-brand{
    color: #000;
    font-size: 25px;
    font-weight: 500;
    text-decoration: unset;
    text-transform: uppercase;
}

.tab_nav_tem .nav-link{
    color: #562894 !important;
    border-radius: 0px !important;
    background: transparent !important;
    border-bottom: 1px solid #f5f5f5;
    padding: 0.8rem 0.5rem;
}
.tab_nav_tem .nav-link.active, .nav-pills .show > .nav-link{
    background: #f7f7f7 !important;
    font-weight: 500;
}
.tab_nav_tem .nav-link:hover{
    background: #f7f7f7 !important;
}
.tab_nav_tem .nav-pills{
    border-top: 2px solid #562894;
}
.right_side_menu .input-group-text{
    background: #fff;
    border: 0px;
    border-left: 1px solid #eee;
    border-radius: 0px;
}
.form_control_feedback{
    font-size: 25px;
    color: #ccc;
    font-weight: normal;
}
.custom_search{
    height: -webkit-fill-available;
    border: 0px;
    border-right: 1px solid #eee;
    border-radius: 0px;
    box-shadow: none;
}
.custom_search:focus{
    box-shadow: none;
    border-color: #eee;
}
.full_screen{
    padding: 12px 20px;
    font-size: 25px;
    color: #000;
}

/* graph stuff */

.field-tick {
    transition: opacity 750ms linear;
  }
  
  .field-tick circle {
    fill: #fff !important;
  }
  
  .field-tick--disabled {
    opacity: 0;
  }
  
  .field-tick circle,
  .field-tick text {
    transition: fill 250ms linear;
    transition-delay: 400ms;
  }
  
  .field-tick text {
    font-size: 10px;
    /* text-anchor: middle; */
  }
  .field-tick .keywordtext{
    font-size: 13px;
  }
  
.field-track{
  fill: none;
  stroke: #000;
  stroke-width: 1.5px;
  stroke-width: 0.5px;
}
.field-tick:hover circle{
  fill: #04CDAE !important;
}
.links{
  transition: 0.3s ease-out;
}
.links:hover{
  /* stroke: #5628943b; */
  stroke: #04CDAE;
}

.links.active{
  /* stroke: #562894; */
  stroke: #04CDAE;
}

.active_circle circle{
  fill: #04CDAE !important;
}
/* tooltip for share in banner */
.tooltipa {
    border-bottom: 1px dotted #000000;
    color: #000000;
    outline: none;
    text-decoration: none;
    position: relative;
  }
  .tooltipa .classic {
    margin-left: -999em;
    position: absolute;
  }
  .tooltipa:hover .classic {
    border-radius: 5px 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    position: absolute;
    right: -0.5em;
    top: 2.5em;
    z-index: 99;
    margin-left: 0;
    width: max-content;
    background: #f7f7f7;
    border: 1px solid #fff;
  }
  .classic ul{
    padding: 0px 10px;
    margin: 0;
    list-style-type: none;
    font-size: 12px;
    text-align: left;
  }
  .classic ul svg{
    font-size: 20px;
    margin-right: 10px;
  }
  .classic ul li a{
      color: #000;
  }
  .classic ul li a:hover{
    color: #562894;
    text-decoration: none;
  }
  .classic ul li{
      margin: 10px;
      border-bottom: 1px solid #000000;
  }
  .classic ul li:last-child{
    border-bottom: 1px solid transparent;
  }
  .classic ul:after{
    position: absolute;
    content: " ";
    bottom: 100%;
    right: 10%;
    margin-left: -5px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent #f7f7f7 transparent;
  }