/*========= Menu button =========*/
.bar1, .bar2, .bar3 {
    width: 20px;
    height: 2px;
    background-color: #55190c;
    margin: 3px 0;
    border-radius: 9px;
    transition: 0.2s;
}
.container {
  display: block;
  width: 35px;
  opacity: .8;
  transition: 0.2s;
}

.container:hover {
    opacity: 1;
}

.close .bar1 {
    -webkit-transform: translate(0px, 12px) rotate(-45deg);
    transform: translate(0px, 12px) rotate(-45deg);
}

.close .bar2 {opacity: 0;}

.close .bar3 {
    -webkit-transform: translate(0, -12px) rotate(45deg); 
    transform: translate(0, -12px) rotate(45deg);
}

/*========= side navigation ========= */
.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    opacity: 0.7;
    text-align: center;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 60px;
    transition: 0.5s;
}

/* The navigation menu links */
.sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.sidenav a:hover {
    color: #f1f1f1;
}

.open .sidenav{
  width: 100%;
}

.menubutton{
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 2;
}